import VColumnItem from "../components/column-item";
import VArticleItem from "../components/article-item";
import VMainSidebar from "../components/main-sidebar";
import home from "../api/home";
export default {
  components: {
    VColumnItem: VColumnItem,
    VArticleItem: VArticleItem,
    VMainSidebar: VMainSidebar
  },
  name: 'list',
  created: function created() {
    this.fetchData();
  },
  data: function data() {
    return {
      article: [],
      column: []
    };
  },
  methods: {
    fetchData: function fetchData() {
      var r;
      return regeneratorRuntime.async(function fetchData$(_context) {
        while (1) {
          switch (_context.prev = _context.next) {
            case 0:
              _context.next = 2;
              return regeneratorRuntime.awrap(home.data());

            case 2:
              r = _context.sent;
              this.article = r.data.data.article;
              this.column = r.data.data.column;

            case 5:
            case "end":
              return _context.stop();
          }
        }
      }, null, this);
    },

    /**
     * 路由跳转
     * @param path
     */
    goLink: function goLink(path) {
      this.$router.push(path);
    }
  }
};